:root{

    --PrimaryColor:#1589ff;
    --PrimaryHoverColor:#0e5fb2;
    --SecondaryColor:#000000;
    --FontColor:#ffffff;
    --MainColor: #023047;
    --MainTrans: #023047f3;
    --MainTrans-2:#023047d7;
    --AboutBackground:#023047f8;
    --HeadingBackground:#02293da6;
    --HeadingText:#1589ff;
    --TypingColor:#458a9c;
    --ProgressBackground:#02131c69;
